@import url(https://fonts.googleapis.com/css2?family=Potta+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Potta+One&display=swap);
.table{border-collapse:collapse;margin-top:60px}.table th{font-size:18px;padding:15px}.table td{padding:10px}

.form{width:800px;position:relative}.form__container{position:relative;display:grid;grid-template-columns:1fr 2.5fr;margin-top:30px}.form__input,.form__select{outline:none;height:48px;font-size:20px;font-family:"Potta One", cursive;padding:5px;color:#525151;border:1px solid transparent}.form__label{display:flex;flex-direction:column;justify-content:center;font-size:18px}.form__button{position:absolute;top:calc(50% - 40px);right:-120px;width:88px;height:88px;background-color:#f7f7f7;border:none;border-radius:50%;font-family:"Potta One", cursive;text-transform:uppercase;font-size:30px;color:grey;cursor:pointer}.form__button:focus,.form__button:active{outline:none}.form__message-error{position:absolute;top:-20px;left:50%;font-family:"Courier New", Courier, monospace;color:#f13636}.form__input-error{border-color:#f13636;box-shadow:0 0 5px 1px #f13636}

*{box-sizing:border-box}body,h1{margin:0}html{font-family:"Potta One", cursive;color:#fffefe}.main{background:linear-gradient(to bottom, #323232 0%, #3f3f3f 40%, #1c1c1c 150%),linear-gradient(to top, rgba(255,255,255,0.4) 0%, rgba(0,0,0,0.25) 200%);background-blend-mode:multiply;height:100vh;display:flex;flex-direction:column;align-items:center;margin-left:-150px}.main__title{margin-top:20px;font-size:36px}

